html {
  text-align: center;
  background: #018ded url("https://unsplash.it/1500/1000?image=881&blur=5") 0 0 / cover;
  font-family: helvetica neue;
  font-size: 10px;
}

body {
  min-height: 100vh;
  flex: 1;
  align-items: center;
  margin: 0;
  font-size: 2rem;
  display: flex;
}

.clock {
  width: 30rem;
  height: 30rem;
  border: 20px solid #fff;
  border-radius: 50%;
  margin: 50px auto;
  padding: 2rem;
  position: relative;
  box-shadow: 0 0 0 4px rgba(0, 0, 0, .1), inset 0 0 0 3px #efefef, inset 0 0 10px #000, 0 0 10px rgba(0, 0, 0, .2);
}

.clock-face {
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateY(-3px);
}

.hand {
  width: 50%;
  height: 6px;
  transform-origin: 100%;
  background: #000;
  transition: all 50ms cubic-bezier(0, 2.7, .58, 1);
  position: absolute;
  top: 50%;
  transform: rotate(90deg);
}

.hour-hand {
  height: 8px;
}

.second-hand {
  background-color: #bc8f8f;
}

